.form {
  padding-top: 30px;
  padding-bottom: 40px;
}

.form__title {
  @include heading-s();
}

.form__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-align: left;
  border: none;
  padding: 10px;
}

.form__label,
.form__input {
  flex-basis: 100%;
  flex-grow: 1;
}

.form__label {
  color: var(--basic-black-lighten);
}

.form__input {
  height: 50px;
  font-family: $font-family-basic;
  font-weight: 400;
  font-style: normal;
  font-size: 15px;
  line-height: 28px;
  background-color: var(--special-white-darken);
  border-radius: 6px;
  border: 2px solid var(--basic-color1);
  padding: 15px;
}

.form__textarea {
  height: 150px;
  resize: none;
}

